import React, { useCallback, useEffect, useRef, useState } from "react";
import SanitasChat, {
  configSanitas,
  SanitasChatInterface,
  SanitasChatRef,
} from "./SanitasChat";
import { SanitasProvider } from "@quillbot/keralty-sanitas-chatbot";
import config from "config";
import configProd from "config-prod";
import configUat from "config-uat";
import MessagesEs from "./Message.es.json";
import MessagesEn from "./Message.en.json";

// Default values for fisrt instance
const defaultValues: configSanitas = {
  languageOverride: "es",
  actionColor: "#00cdac",
  backgroundColor: "#00cdac",
  name: "",
  email: "",
  customAttributes: { cType: "support" },
};

function App() {
  //Ref of chat
  const sanitasChatRef = useRef<SanitasChatRef>(null);
  //State for manage data
  const [dataConfig, setDataConfig] = useState<configSanitas>(defaultValues);

  const onHide = useCallback(() => {
    setTimeout(async () => {
      if (sanitasChatRef) {
        sanitasChatRef?.current?.onHideChat();
      }
    });
    if (window && (window as any).ReactNativeWebView) {
      (window as any).ReactNativeWebView.postMessage("hideChat");
    }
  }, []);

  const handlerShow = useCallback(() => {
    setTimeout(async () => {
      if (sanitasChatRef) {
        sanitasChatRef?.current?.onShowChat();
      }
    });
  }, []);

  const handlerHide = useCallback(() => {
    setTimeout(async () => {
      if (sanitasChatRef) {
        sanitasChatRef?.current?.onHideChat();
      }
    });
  }, []);

  const onShow = useCallback(() => {
    if (window && (window as any).ReactNativeWebView) {
      (window as any).ReactNativeWebView.postMessage("showChat");
    }
  }, []);

  const changeData = useCallback((data: configSanitas) => {
    setDataConfig(data);
    console.log(data);
    sanitasChatRef?.current?.setChatData(data);
  }, []);

  if (!window["Sanitas_native"]) {
    window["Sanitas_native"] = {
      hideChat: handlerHide,
      showChat: handlerShow,
      setConfig: changeData,
    };
  }

  console.log(dataConfig.languageOverride === "en" ? MessagesEn : MessagesEs);
  console.log("languagee", dataConfig.languageOverride);

  return (
    <SanitasProvider
      appId={"chat_key"}
      onHide={onHide}
      onShow={onShow}
      config={configUat}
      Messages={dataConfig.languageOverride === "en" ? MessagesEn : MessagesEn}
    >
      <SanitasChat ref={sanitasChatRef} dataConfig={dataConfig} />
    </SanitasProvider>
  );
}

export default App;
